import * as React from 'react';
import { Table, TableBody, TableCell, TableHead, TableRow, TablePagination } from '@material-ui/core';
import './component-style.scss';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';

interface IProps {
    className?: string;
    cols?: string[];
    rows: any[];
    


    renderHeader?: () => JSX.Element;
    renderRow?: (rowData: any, index: number) => JSX.Element;

    /* 
     * Use this to stylize the column cells.
     * colIndex: the column index you want to style.
     * value: give you control to manipulate the value.
     */
    onFormat?: (colIndex: number, value: any) => any;

    /*
     * Use this to stylize the column headers.
     * colIndex: the column index you want to style.
     * name: the header name at will appear.
     */
    onHeaderFormat?: (colIndex: number, name: string) => JSX.Element;

    rowsPerPageOptions?: number[];
    labelRowsPerPage?: string;
    onChangeInfo?: (info: LabelDisplayedRowsArgs) => string;
}

interface IStates {
    page: number;
    rowsPerPage: number;
}

export class CustomTable extends React.PureComponent<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            page: 0,
            rowsPerPage: 0
        }
    }

    componentDidMount() {
        if (this.props.rowsPerPageOptions) {
            this.setState({rowsPerPage: this.props.rowsPerPageOptions[0]});
        }
    }

    handleChangePage = (_event: unknown, page: number) => {
        this.setState({page: page});
    }
    
    handleChangeRowsPerPage = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.setState({
            rowsPerPage: parseInt(event.target.value, 10),
            page: 0
        });
    }

    getSlicedData = () => {
        if (this.props.rowsPerPageOptions) {
            const startingItem = this.state.page * this.state.rowsPerPage;
            return this.props.rows.slice(startingItem, startingItem + this.state.rowsPerPage);
        } else {
            return this.props.rows;
        }
    }

    

    render(): JSX.Element {
        return (
            <>
            <Table className={(this.props.className ? this.props.className : '') + ' table'}>
                <TableHead>
                    <TableRow>
                        {this.props.renderHeader ? (
                            this.props.renderHeader()
                        ) : (
                            this.props.cols ? (
                                this.props.cols.map((colName, index) => {
                                    return (
                                        this.props.onHeaderFormat ? (
                                            this.props.onHeaderFormat(index, colName)
                                        ) : (
                                            <TableCell key={index}>{colName}</TableCell>
                                        )
                                    );
                                })
                            ) : null
                        )}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {this.getSlicedData().map((row, j) => {
                        const backgroundColor = (j % 2 == 0 ? '#FFFFFF' : '#F6F7FA');
                        
                        return this.props.renderRow ? (
                            this.props.renderRow(row, j)
                        ) : (
                            <TableRow key={j} style={{backgroundColor: backgroundColor}}>
                                {Object.keys(row).map((value, j) => {
                                    return (this.renderCell(j, row[value]));
                                })}
                            </TableRow>
                        )
                        
                    })}
                </TableBody>
            </Table>
            {this.props.rowsPerPageOptions ? (
                <TablePagination
                    className='pagination MuiTablePagination-root'
                    rowsPerPageOptions={this.props.rowsPerPageOptions}
                    count={this.props.rows.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    labelDisplayedRows={(info: LabelDisplayedRowsArgs) => {
                        return (
                            this.props.onChangeInfo ? (
                                this.props.onChangeInfo(info)
                            ) : (
                                `${info.from}-${info.to} of ${info.count}`
                            ));
                    }}
                    labelRowsPerPage={
                        this.props.labelRowsPerPage ? (
                            this.props.labelRowsPerPage
                        ) : (
                            'Rows per page:'
                        )}
                    onChangePage={(_event: unknown, page: number) => {
                        this.setState({page: page});
                    }}
                    onChangeRowsPerPage={(event: React.ChangeEvent<HTMLInputElement>) => {
                        this.setState({
                            rowsPerPage: parseInt(event.target.value, 10),
                            page: 0
                        });
                    }} />
            ) : null}
            </>
        );
    }

    renderCell(index: number, value: any): JSX.Element {
        return (
            <TableCell key={index}>
                {this.props.onFormat ? this.props.onFormat(index, value) : value}
            </TableCell>
        );
    }
}