
import * as React from 'react';

// import { SummarySectionProps } from 'Pages/CustomerCarePage/UpdateTicketPage/UpdateTicketProps';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { Grid, TableCell } from '@material-ui/core';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { UpdateData, AccommodationData, DiagnosisData, PhysicianData, ProcedureData, ItemData } from '../UpdateTicketProps';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';

interface IProps {
    ticketNo: string;
    updates: UpdateData[];
    accommodationsData: AccommodationData[];
    diagnosesData: DiagnosisData[];
    physiciansData: PhysicianData[];
    proceduresData: ProcedureData[];
    itemsData: ItemData[];

    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;

    coverage: DropdownChoice;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate: Date | null;
    setTabSelected: (value: number) => void;
}

export class SummarySection extends React.Component<IProps> {

    constructor(props: IProps) {
        super(props);
    }

    getTotalAccommodationCost = () => {
        let totalCost = 0;
        this.props.accommodationsData.map(accommodation => {
            totalCost += accommodation.costPerDay * accommodation.days;
        });
        return totalCost;
    }

    getTotalPhysicianCost = () => {
        let totalCost = 0;
        this.props.physiciansData.map(physician => {
            totalCost += physician.amount;
        });
        return totalCost;
    }

    getTotalProcedureCost = () => {
        let totalCost = 0;
        this.props.proceduresData.map(procedure => {
            totalCost += procedure.amount;
        });
        return totalCost;
    }

    getTotalItemCost = () => {
        let totalCost = 0;
        this.props.itemsData.map(item => {
            totalCost += item.amount;
        });
        return totalCost;
    }

    render(): JSX.Element {

        return (
            <div className='section'>
                <div className='header-1'>Review Summary</div>

                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton 
                            label='Add / Edit update' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(2);
                            }}/>
                    </div>
                    <div className='header-2'>UPDATES</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Remarks', 'As of Date', 'Running Hospital Bill']} 
                        rows={this.props.updates}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 2 ? (
                                    <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            return (
                                colIndex == 1 ? (
                                    <div>{GlobalFunction.toCommonDateString(value)}</div>
                                ) : colIndex == 2 ? (
                                    <div style={{textAlign: 'right'}}>
                                        {GlobalFunction.formatNumber(value) + '.00 PHP'}
                                    </div>
                                ) : (value)
                            );
                        }} />
                </div>
                
                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton
                            label='Add / Edit room' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(3);
                            }}/>
                    </div>
                    <div className='header-2'>ROOM ACCOMMODATIONS</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Room Type', 'No. of Days', 'Cost Per Day']} 
                        rows={this.props.accommodationsData}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 1 || colIndex == 2 ? (
                                    <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            const textAlign = (colIndex != 0 ? 'right' : 'left');
                            return (
                                <div style={{textAlign: textAlign}}>
                                    {colIndex == 2 ? 
                                        GlobalFunction.formatNumber(value) + '.00 PHP' : 
                                        value}
                                </div>
                            );
                        }} />
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(this.getTotalAccommodationCost()) + '.00 PHP'}
                            </span>
                        </div>
                    </div>
                </div>

                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton 
                            label='Add / Edit diagnosis' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(4);
                            }}/>
                    </div>
                    <div className='header-2'>DIAGNOSES</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Diagnosis', 'Remarks', 'Type', 'Primary Diagnosis?']} 
                        rows={this.props.diagnosesData}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 0 || colIndex == 1 ? (
                                    <TableCell style={{minWidth: '250px'}}>{value}</TableCell>
                                ) : colIndex == 3 ? (
                                    <TableCell style={{minWidth: '200px', textAlign: 'center'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '100px', textAlign: 'center'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            return (
                                colIndex == 0 ? (<div>{value.value + " - " + value.label}</div>) : 
                                colIndex == 2 ? (<div style={{textAlign: 'center'}}>{value ? 'Final' : 'Initial'}</div>) : 
                                colIndex == 3 ? (<div style={{textAlign: 'center'}}>{value ? 'Yes' : 'No'}</div>) : 
                                (value)
                            );
                        }} />
                </div>
                
                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton 
                            label='Add / Edit physicians' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(5);
                            }}/>
                    </div>
                    <div className='header-2'>PHYSICIANS</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Specialization - Doctor Name', 'Diagnosis', 'Remarks', 'No. of Visits', 'Amount']} 
                        rows={this.props.physiciansData}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 0 ? (
                                    <TableCell style={{minWidth: '250px'}}>{value}</TableCell>
                                ) : colIndex == 1 ? (
                                    <TableCell style={{minWidth: '100px'}}>{value}</TableCell>
                                ) : colIndex == 3 || colIndex == 4 ? (
                                    <TableCell style={{minWidth: '150px', textAlign: 'right'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            return (
                                colIndex == 0 ? (
                                    <div>
                                        <div>{value.specialization + ' - ' + value.name}</div>
                                        <div className='light-text'>{value.type + ' Physician'}</div>
                                    </div>
                                ) : colIndex == 1 ? (
                                    <div>{value.detail}</div>
                                ) : colIndex == 3 || colIndex == 4 ? (
                                    <div style={{textAlign: 'right'}}>
                                        {colIndex == 4 ? GlobalFunction.formatNumber(value) + '.00 PHP' : value}
                                    </div>
                                ) : (value)
                            );
                        }} />
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(this.getTotalPhysicianCost()) + '.00 PHP'}
                            </span>
                        </div>
                    </div>
                </div>
                
                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton 
                            label='Add / Edit procedures' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(6);
                            }}/>
                    </div>
                    <div className='header-2'>PROCEDURES</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Procedure', 'Diagnosis', 'Doctor', 'Date', 'Remarks', 'Amount']} 
                        rows={this.props.proceduresData}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 0 ? (
                                    <TableCell style={{minWidth: '200px'}}>{value}</TableCell>
                                ) : colIndex == 1 ? (
                                    <TableCell style={{minWidth: '100px'}}>{value}</TableCell>
                                ) : colIndex == 2 || colIndex == 3 ? (
                                    <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
                                ) : colIndex == 5 ? (
                                    <TableCell style={{textAlign: 'right', minWidth: '100px'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '150px'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            return (
                                colIndex == 0 ? (
                                    <div>{value.detail + ' - ' + value.label}</div>
                                ) : colIndex == 1 ? (
                                    <div>{value.detail}</div>
                                ) : colIndex == 2 ? (
                                    <div>{value.label}</div>
                                ) : colIndex == 3 ? (
                                    <div>{GlobalFunction.toCommonDateString(value)}</div>
                                ) : colIndex == 5 ? (
                                    <div style={{textAlign: 'right'}}>
                                        {GlobalFunction.formatNumber(value) + '.00 PHP'}
                                    </div>
                                ) : (value)
                            );
                        }} />
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(this.getTotalProcedureCost()) + '.00 PHP'}
                            </span>
                        </div>
                    </div>
                </div>
                
                <div className='sub-section'>
                    <div style={{float: 'right'}}>
                        <TextButton 
                            label='Add / Edit item' 
                            onClick={() => {
                                window.scroll(0, -window.pageYOffset);
                                this.props.setTabSelected(8);
                            }}/>
                    </div>
                    <div className='header-2'>OTHER DETAILS</div>
                    <CustomTable 
                        className='custom-table'
                        cols={['Item', 'Type', 'Amount']} 
                        rows={this.props.itemsData}
                        onHeaderFormat={(colIndex: number, value: string) => {
                            return (
                                colIndex == 2 ? (
                                    <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
                                ) : (
                                    <TableCell style={{minWidth: '300px'}}>{value}</TableCell>
                                )
                            );
                        }} 
                        onFormat={(colIndex: number, value: any) => {
                            return (
                                colIndex == 2 ? (
                                    <div style={{textAlign: 'right'}}>
                                        {GlobalFunction.formatNumber(value) + '.00 PHP'}
                                    </div>
                                ) : (value)
                            );
                        }} />
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(this.getTotalItemCost()) + '.00 PHP'}
                            </span>
                        </div>
                    </div>
                </div>
                
                <div className='sub-section'>
                    <Grid container>
                        <Grid item xs={4}>
                            <div className='header-2'>PHILHEALTH</div>
                            <div className='sub-section'>
                                <div className='light-text'>COVERAGE</div>
                                <div className='small-text'>{this.props.coverage.label}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>AMOUNT</div>
                                <div className='small-text'>{GlobalFunction.formatNumber(this.props.philhealthAmount) + ".00 PHP"}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>TICKET NUMBER</div>
                                <div className='small-text'>{this.props.ticketNo}</div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <div className='header-2'>CONFINEMENT</div>
                            <div className='sub-section'>
                                <div className='light-text'>PROVIDER</div>
                                <div className='small-text'>{this.props.provider.value + ' - ' + this.props.provider.label}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>DATE ADMITTED</div>
                                <div className='small-text'>{GlobalFunction.toCommonDateString(this.props.dateAdmitted)}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>KIND OF CONFINEMENT</div>
                                <div className='small-text'>{this.props.confinementKind}</div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <div className='header-2'>DISCHARGE DETAILS</div>
                            <div className='sub-section'>
                                <div className='light-text'>FINAL HOSPITAL BILL AMOUNT</div>
                                <div className='small-text'>{GlobalFunction.formatNumber(this.props.finalBill) + '.00 PHP'}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>DISCHARGE DATE</div>
                                <div className='small-text'>
                                    {this.props.dischargeDate ? (
                                        GlobalFunction.toCommonDateString(this.props.dischargeDate)
                                    ) : '---'}
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton 
                                label='Edit PhilHealth' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(7);
                                }}/>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton 
                                label='Edit Confinement Details' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(3);
                                }}/>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton
                                label='Edit Discharge Details' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(7);
                                }}/>
                        </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }
}